function fixElementOffSet(target, offsetelement) {
    console.log("fixelementoffset");
    var offSetHeight = jQuery(offsetelement).height();
    var scrollTo = jQuery(target).offset().top - offSetHeight;
    console.log(scrollTo);
    jQuery('html,body').animate({
        scrollTop: scrollTo
    }, 1500);
}
jQuery(function() {
    if (window.location.hash) {
        console.log(window.location.hash);
        target = window.location.hash;

        if (jQuery(target).length) {

            console.log("hash existe");
            /* go to element*/
            setTimeout(function() {
                fixElementOffSet(target, jQuery("header"));
            }, 500);
        } else {
            console.log("no hay elemento");
        }
    }
})